// HistoricalRatesChart.js
import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';
import { fetchHistoricalRates } from '../api';

const HistoricalRatesChart = ({ fromCurrency, toCurrency }) => {
  const [historicalData, setHistoricalData] = useState([]);

  useEffect(() => {
    const fetchData = async () => {
      const endDate = new Date().toISOString().split('T')[0];
      const startDate = new Date(new Date().setFullYear(new Date().getFullYear() - 1)).toISOString().split('T')[0]; // 1 year ago
      const data = await fetchHistoricalRates(fromCurrency, toCurrency, startDate, endDate);
      if (data && data.rates) {
        const formattedData = Object.keys(data.rates).map(date => ({
          date,
          rate: data.rates[date][toCurrency]
        }));
        setHistoricalData(formattedData);
      }
    };
    fetchData();
  }, [fromCurrency, toCurrency]);

  return (
    <ResponsiveContainer width="100%" height={400}>
      <LineChart data={historicalData}>
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="date" />
        <YAxis />
        <Tooltip />
        <Legend />
        <Line type="monotone" dataKey="rate" stroke="#8884d8" activeDot={{ r: 8 }} />
      </LineChart>
    </ResponsiveContainer>
  );
};

export default HistoricalRatesChart;
